var _self;
export default {
	data() {
		return {
			td_with:["width:136upx;","width:411upx;white-space: normal;overflow: visible;","width:139upx"],
			data: { 
				thead: ['设备ID', '报警内容', '报警时间'],
				tbody: [
					[
						'14525', //id
						'当前扬尘为PM9.8，重度污染和大家好 逆剑狂神你反锁逆剑狂神你反锁逆剑狂神你反锁逆剑狂神你反锁',  //报警内容
						'15:38 2019/10/14', //报警时间
					],
				]
			},
			project_id:0,
			wrong_name:"",
			
		};
	},
	onLoad(option) {
		 let  optioninfo=JSON.parse(option.query);
		 this.project_id = optioninfo.id;
		 this.wrong_name = optioninfo.name;
		_self = this;
		_self.getDetailinfo();
	},
	methods: {
		getDetailinfo(){
			uni.showLoading({
				title:'努力加载中...'
			})
	
			_self.$ajax({
				path: _self.$api.alarmDetails,
				data:{
					project_id:this.project_id,
					wrong_name:this.wrong_name
				},
				success(res){
					
					// _self.time = res.data.times;
					
					uni.hideLoading();
				
					if(res.data.code == 2000 ){
						console.log(res.data.data);
						
						
					
					}else{
						uni.redirectTo({
							url:"/pages/user/login"
						})
						
					}
				}
			})
		},
	}
};

